.colliers-full-page {
  position: relative;
  left: -30px;
  top: -20px;
  bottom: 0;
  width: calc(100% + 60px);
  padding: 20px 30px;
  margin-bottom: -40px;
  background-size: cover;
  background-position: center;
  @include print {
    left: -0.5in;
    top: -0.5in;
    width: 8.5in;
    padding: 0.5in;
  }
}